div#colors >section {
 flex-wrap: wrap;
 display: flex;
 align-items : center;
 justify-content: center;
 width: 10rem;
 background: blueviolet;
 border-radius: 6px;
padding: 2px;
/* transition: all 2s ease-in-out; */
animation: colors 1s ease-in-out alternate;
}
div#colors button{
 width: 30px;
 height: 30px;
 display: block;
 margin: 6px;
 border: solid 0.2px;
 border-radius: 6px;
 outline: none;
 transition: all 0.25s ease-in-out;
animation: btns 1s 1s ease-in-out alternate;
}
div#colors button:hover {
 filter: contrast(1.06);
 transform: scale(1.05);
}
@keyframes colors {
 0%{
  transform: scale(0.1) ;
  transform-origin: 50% 0;
 }
 20%{
background-color: rgb(81, 103, 111);
transform: scale(0.2) ;

 }
 30%{
  background-color: rgb(81, 103, 111);
  transform: scale(0.3) ;
  
   }
   40%{
    background-color: rgb(81, 103, 111);
    transform: scale(0.4) ;
    
     } 50%{
      background-color: rgb(81, 103, 111);
      transform: scale(0.5) ;
      
       }
}
@keyframes btns {
 0%{
  transform: scale(1.09);
  filter: contrast(3);
 }
}
